#video, #photo {
  width: 100%;
  height: 100%;
  border: 1px solid #000;
  box-shadow: 2px 2px 3px #000;
}

#canvas {
  display: none;
}

.camera {
  width: 100%;
  height: 100%;
  display: inline-block;
}

.output {
  width: 100%;
  vertical-align: top;
  display: inline-block;
}

#startbutton {
  color: #fff;
  background-color: #00960080;
  border: 1px solid #ffffffb3;
  margin-left: auto;
  margin-right: auto;
  font-family: Lucida Grande, Arial, sans-serif;
  font-size: 14px;
  display: block;
  position: relative;
  bottom: 32px;
  box-shadow: 0 0 1px 2px #0003;
}

.contentarea {
  width: 100%;
  height: 100%;
  font-family: Lucida Grande, Arial, sans-serif;
  font-size: 16px;
}

svg:not(:root) {
  display: block;
}

.playable-code {
  color: #4d4e53;
  height: 100px;
  width: 90%;
  background-color: #f4f7f8;
  border: none;
  border-left: 6px solid #558abb;
  padding: 10px 10px 0;
}

.playable-canvas {
  border: 1px solid #4d4e53;
  border-radius: 2px;
}

.playable-buttons {
  text-align: right;
  width: 90%;
  padding: 5px 10px 5px 26px;
}

html, body {
  min-height: 100%;
}

